<template>
  <div id="app">
    <v-modal title="Video play"
             :visible="visible"
             :width="380"
             okText="Pause"
             cancelText="Cancel"
             @ok="modelPause"
             @cancel="modelCancel">
        
        <video-player class="video-player-box"
                    ref="videoPlayer"
                    :options="playerOptions"
                    :playsinline="true"
                    @play="onPlayerPlay($event)"
                    @pause="onPlayerPause($event)"
                    @ended="onPlayerEnded($event)"
                    @loadeddata="onPlayerLoadeddata($event)"
                    @waiting="onPlayerWaiting($event)"
                    @playing="onPlayerPlaying($event)"
                    @timeupdate="onPlayerTimeupdate($event)"
                    @canplay="onPlayerCanplay($event)"
                    @canplaythrough="onPlayerCanplaythrough($event)"
                    @ready="playerReadied"
                    @statechanged="playerStateChanged($event)">
        </video-player>
    </v-modal>
    <!-- Item -->
    <ul id="music-ul">
        <li v-for="(item,index) in items" :key="index">
          <MusicItem :item="item" @playVideo="playVideo"/>
        </li>
    </ul>

    <div class="loadmore">
      <v-button type="success" @click="getMusicItem">加载更多</v-button>
    </div>
  </div>
</template>

<script>

import MusicItem from './components/MusicItem.vue'

export default {
  name: 'app',
  components: {
    MusicItem
  },
  mounted(){
    this.getMusicItem();
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player
    }
  },
  methods:{
    getMusicItem(){
      var limit = 10;
      var offset = limit *this.page;
      // 获取mv列表
      this.axios.get("https://api.bzqll.com/music/tencent/hotMvList?key=579621905&year=0&tag=0&area=0&limit="+limit+"&offset="+offset).then((response) => {
          if(response.data.code == 200){
            this.page = this.page+1;
            // this.items = response.data.data;
            // console.log("1");
            // console.log(this.items);
            this.items = this.items.concat(response.data.data);
            console.log(this.items.length);
            // console.log(this.items);
          }
          
      });
    },
    showModal() {
        this.visible = true;
    },
    modelPause() {
      this.$refs.videoPlayer.player.pause();
    },
    modelCancel() {
        this.visible = false;
    },
    playVideo(item){
      console.log(JSON.stringify(item));
      this.playerOptions =  {
          // videojs options
          width: '360',
          muted: false,
          language: 'en',
          playbackRates: [0.7, 1.0, 1.5, 2.0],
          sources: [{
            type: "video/mp4",
            src: item.url
          }],
          poster: item.pic,
        };
      this.showModal();
    },
    // listen event
    onPlayerPlay(player) {
      // console.log('player play!', player)
    },
    onPlayerPause(player) {
      // console.log('player pause!', player)
    },
    onPlayerEnded(player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata(player) {
      // console.log('player Loadeddata!', player)
    },
    onPlayerWaiting(player) {
      // console.log('player Waiting!', player)
    },
    onPlayerPlaying(player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate(player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay(player) {
      console.log('player Canplay!', player)
      player.play()
    },
    onPlayerCanplaythrough(player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged(playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied(player) {
      // console.log('example 01: the player is readied', player)
    }
  },
  data(){
    return {
      items: [],
      playerOptions: {},
      visible:false,
      page:0
    }
  }
}
</script>

